:root {
  font-size: 14px;
}
@media screen and (max-width: 550px) {
  :root {
    font-size: 2.5vmin;
  }
}
.app {
  color: white;
}
nav {
  height: 5rem;
  background-color: #202c40;
  color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
}
nav .title {
  font-size: 1.5rem;
  margin: 2rem;
}
nav .btn {
  font-size: 1.2rem;
  margin: 1rem;
  padding: 1rem;
  border: none;
  color: white;
  background-color: #ffffff00;
  transition: background-color 0.3s ease;
  cursor: pointer;
}
nav .btn:hover {
  background-color: rgba(255, 255, 255, 0.164);
}
nav .btn:active {
  background-color: rgba(255, 255, 255, 0.342);
}
nav .push {
  margin-left: auto;
}
.loading {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 3rem;
  background-color: rgba(53, 53, 53, 0.486);
  border-radius: 2rem;
}
.users {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: auto auto auto;
}

@media screen and (max-width: 95rem) {
  .users {
    grid-template-columns: auto auto;
  }
}
@media screen and (max-width: 67.5rem) {
  .users {
    grid-template-columns: auto;
  }
}
.users > div {
  margin: 3rem auto;
  padding: 3rem;
  display: flex;
  align-items: center;
  animation: rot 1s ease;
  background-color: rgba(37, 37, 37, 0.596);
  border-radius: 1rem;
}
@keyframes rot {
  0% {
    transform: scale(1.2);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
.avatar > img {
  width: 9.14286rem;
  height: 9.14286rem;
}
.avatar {
  border-radius: 20%;
  overflow: hidden;
  width: 9.14286rem;
  height: 9.14286rem;
  border: solid 0.3rem grey;
}
.info {
  margin: 2rem;
  width: 15rem;
}
.name {
  font-size: 1.5rem;
  margin: 0.5rem;
  padding: 0.5rem;
}
.email {
  font-size: 1.2rem;
  margin: 0.5rem;
  padding: 0.5rem;
}

/* Loading css */
.lds-ripple {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.lds-ripple div:nth-child(2) {
  animation-delay: -0.5s;
}
@keyframes lds-ripple {
  0% {
    top: 36px;
    left: 36px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 72px;
    height: 72px;
    opacity: 0;
  }
}
